/**
 *  ajax, axios url path 
 *  @module utils/ajax
 *  @author beixian.huang@brilliant-solution.com
 *
 */

import pathToRegex from 'path-to-regexp'
import axios from './axios'
import {getHost} from './url'
import {get, save, LOCAL, SESSION} from './storage'
import bus from './bus'
import {guid} from './util'
import * as config from '../config'
import ElementUI from "element-ui";


/**
 * ajaxjsoncode  /src/config.js 
 * @const
 * @type {number}
 * @default 0
 */
const AJAX_SUCCESS = config.AJAX_SUCCESS || 0

// url
let pathToRegexCaches = {}

// 
let Caches = {}

/**
 * ajax 
 * @const
 * @type {{url: null, method: string, params: null, data: null}}
 * @property {string} url  - , 
 * @property {string} method - get
 * @property {object} params - path: /api/user/:id, url, null
 * @property {object} data - get head deletepost, null,  Object/URLSearchParams/FormData
 * @property {Object|boolean} cache 
 * @property {boolean} socket webSocket
 */
const defaultConfig = {
    url: null,
    method: 'get',
    params: null, // path: /api/user/:id
    data: null, // get head deletepost
    cache: null, // boolean/Object, Object: {local, session, key} local:localStoragesession:SessionStorage keykeyurl+query
    socket: false // boolean,  websocket
}

/**
 * 
 * @param {Object} res 
 * @param {object} cache 
 * @param {object} storage 
 * @param {function} resolve
 * @param {function} reject
 */
function processData(res, cache, storage, resolve, reject) {
    if (res.data) {
        //   if (Number.parseInt(res.data.code) === AJAX_SUCCESS) {
        if (res.data.success === AJAX_SUCCESS) {
            (cache && cache.key) && saveCache(cache.key, res.data.data, storage)
            resolve(res.data.data)
        } else {
            if (res.data.message){
                ElementUI.Message({type: 'warning', message:res.data.message})
            }else {
                ElementUI.Message({type: 'error', message:''})
            }
            reject(res.data)
        }
    }
}

/**
 *
 * ajaxPromiseajaxjsoncode [AJAX_SUCCESS]{@link module:utils/ajax~AJAX_SUCCESS}   reject
 * @inner
 * @param {string} type 
 * @param {string} url url
 * @param {Object} data 
 * @param {Object} config axios
 * @param {Boolean} socket websocket
 * @param {object} cache 
 * @returns {Promise} Promise 
 */
function createPromise(type, url, data, config, socket, cache = {}) {
    let cacheData, storage = null
    if (cache && cache.key) {
        storage = cache.local ? LOCAL : (cache.session ? SESSION : null)
        cacheData = getCache(cache.key, storage)
    }

    return new Promise(function (resolve, reject) {
        if (cacheData) {
            resolve(cacheData)
        } else {
            // websocket
            if (socket) {
                // websocket
                data = {
                    // websocket
                    __async__: guid(),
                    ...data
                }
                // websocket
                // 
                bus.$once(data.__async__, res => {
                    processData(res, cache, storage, resolve, reject)
                })
            }
            const http = ['get', 'head', 'delete'].includes(type)
                ? axios[type](url, {...config, params: data})
                : axios[type](url, data, config)
            http.then(function (res) {
                // websockethttp
                if (socket) return
                processData(res, cache, storage, resolve, reject)
            }).catch(function (res) {

                reject(res)
            })
        }
    })
}


/**
 * key, url
 * @param {string} url url
 * @param {string} type 
 * @param {object} params url
 * @param {object} data 
 * @return {string}
 */
function createKey(url, type, params, data) {
    return encodeURIComponent([url, type, JSON.stringify(params), JSON.stringify(data)].join(','))
}

/**
 * 
 * @param {string} key key
 * @param {Storage} storage localStorage/sessionStorage/null
 * @return {String|Object|Array}
 */
function getCache(key, storage) {
    return storage ? get(key, storage) : Caches[key]
}

/**
 * 
 * @param {string} key key
 * @param {Object|Array} data 
 * @param {Storage} storage localStorage/sessionStorage/null
 */
function saveCache(key, data, storage) {
    if (storage) {
        save(key, data, storage)
    } else {
        Caches[key] = data
    }
}

/**
 * ajax 
 * @export
 * @param {object} options - ajax. []{@link module:utils/ajax~defaultConfig}
 * @returns {Promise} promise
 *
 *  @example
 *
 *  // 
 *  ajax({
 *    url: '/api/users'
 *  })
 *  .then(res => {
 *    // to do something...
 *  })
 *  .catch(e => {
 *    // to do something...
 *  })
 *
 *  @example
 *
 *  // url path
 *  ajax({
 *    method: 'get',
 *    url: '/api/users/:id',
 *    params: {
 *      id: '123'
 *    }
 *  })
 *
 *  @example
 *
 *  // 
 *  ajax({
 *    url: '/api/users',
 *    method: 'post',
 *    data: {
 *      name: 'kenny',
 *      password: '123456'
 *    }
 *  })
 *
 *  @example
 *  // 
 *  ajax({
 *    url: '/api/users/',
 *    data: {
 *      page: 1,
 *      limit: 10
 *    },
 *    headers: {
 *      'Content-type': 'application/x-www-form-urlencoded'
 *    }
 *  })
 *
 *  @example
 *
 *  // 
 *  ajax({
 *    url: '/api/user',
 *    cache: true
 *  })
 *
 * // LocalStorage
 *  ajax({
 *    url: '/api/user',
 *    cache: {
 *      local: true
 *    }
 *  })
 *
 * // SessionStorage
 *  ajax({
 *    url: '/api/user',
 *    cache: {
 *      session: true
 *    }
 *  })
 *
 * // key
 *  ajax({
 *    url: '/api/user',
 *    cache: {
 *      local: true,
 *      key: 'cachekey'
 *    }
 *  })
 *
 *  @example httpwebsocketwebsocket
 *  ajax({
 *    url: '/api/user',
 *    socket: true
 *  })
 *
 *  // webscoket 
 *  ws.on('FwzxSyncCall', function (res) {
 *        if (res && res.NotifyId) {
 *         bus.$emit(res.NotifyId, responseData({data: res.Data}))
 *       }
 *     })
 *
 */
export default function (options) {
    // 
    let config = Object.assign({}, defaultConfig, options || {})
    // url
    if (!config.url) {
        throw new Error('ajax url is required!')
    }
    // url
    // url  /api/path/:id
    // urlurl
    let compileCache = pathToRegexCaches[config.url]
    let host = getHost(config.url)
    if (!compileCache) {
        // hosthost
        compileCache = pathToRegexCaches[config.url] = pathToRegex.compile(config.url.replace(host, ''))
    }
    // urlhost
    let url = host + compileCache(config.params)
    let type = config.method.toLowerCase()
    let data = config.data
    // keykey
    let cache = config.cache
        ? {key: createKey(url, type, config.params, data), ...config.cache}
        : {};
    let socket = config.socket

    delete config.url
    delete config.type
    delete config.data
    delete config.params
    delete config.cache
    delete config.socket

    return createPromise(type, url, data, config, socket, cache)
}


/**
 * axios , 
 * @param {Function} transformer 
 * @param {string} method 
 * @param {object} postData 
 * @param {object} params url
 * @returns {Function}
 */
export function transformHandler(transformer, method, postData, params) {
    return function (data) {
        let json = typeof data === 'string' ? JSON.parse(data) : data
        return transformer(json, method, postData, params)
    }
}
